'use client';
import { Card, CardContent } from "@/components/ui/card";
import { TabsContent } from "@/components/ui/tabs";
import { CheckCircle } from "lucide-react";

export default function Overview() {
    return (
        <TabsContent value="overview" className="mt-8">
            <Card>
                <CardContent className="p-6">
                    <h2 className="text-2xl font-bold text-foreground mb-4">What you&apos;ll learn</h2>
                    <div className="grid md:grid-cols-2 gap-3 mb-8">
                        {[
                            "Build professional websites from scratch",
                            "Master HTML5, CSS3, and JavaScript",
                            "Create responsive designs for all devices",
                            "Work with React and modern frameworks",
                            "Build full-stack applications with Node.js",
                            "Deploy applications to production",
                        ].map((item, index) => (
                            <div key={index} className="flex items-start gap-2">
                                <CheckCircle className="h-5 w-5 text-primary mt-0.5 shrink-0" />
                                <span className="text-muted-foreground">{item}</span>
                            </div>
                        ))}
                    </div>

                    <h2 className="text-2xl font-bold text-foreground mb-4">Requirements</h2>
                    <ul className="list-disc list-inside text-muted-foreground space-y-2 mb-8">
                        <li>A computer with internet connection</li>
                        <li>No prior programming experience required</li>
                        <li>Willingness to learn and practice</li>
                    </ul>

                    <h2 className="text-2xl font-bold text-foreground mb-4">Description</h2>
                    <p className="text-muted-foreground mb-4">
                        Welcome to the Complete Web Development Bootcamp! This comprehensive course will take you from complete beginner to professional web developer. You&apos;ll learn everything you need to know to build modern, responsive websites and web applications.
                    </p>
                    <p className="text-muted-foreground">
                        Throughout the course, you&apos;ll work on real-world projects and build a professional portfolio that will help you land your dream job. Join thousands of students who have already transformed their careers with this course.
                    </p>
                </CardContent>
            </Card>
        </TabsContent>
    )
}